import React from 'react'
// var React = require('react')
// synonymous

var div = React.DOM.div
var h1 = React.DOM.h1


var MyTitle = React.createClass({
  render: function(){
    return (
      div(null,
        h1({style: {color: this.props.color, fontWeight: 'bold'}}, this.props.title)
      )
    )
  }
})

export default MyTitle



